.fraud-dashboard-interface {
  display: flex;
  flex-direction: column;
  height: 400px;
}

.network-graph {
  width: 100%;
  border-radius: var(--border-radius);
  overflow: hidden;
  background-color: var(--bg-color);
  border: 1px solid var(--border-color);
  margin: 0.5rem 0;
}

.graph-header {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: var(--surface-hover);
  border-bottom: 1px solid var(--border-color);
  gap: 0.5rem;
  position: relative;
}

.graph-icon {
  color: var(--primary-color);
}

.risk-indicator {
  position: absolute;
  right: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--fraud-risk-color);
  font-size: 0.8rem;
  font-weight: 500;
  animation: pulse 2s infinite;
}

.risk-icon {
  color: var(--fraud-risk-color);
}

@keyframes pulse {
  0% { opacity: 0.7; }
  50% { opacity: 1; }
  100% { opacity: 0.7; }
}

.graph-container {
  position: relative;
  height: 100%;
  overflow: hidden;
  flex: 1;
  padding: 5px; /* Add a small padding inside the graph container */
}

.dark-theme .graph-container {
  background-color: rgba(255, 255, 255, 0.02);
}

.graph-node {
  position: absolute;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.5s ease;
  min-width: 5px; /* Smaller min size */
  min-height: 5px; /* Smaller min size */
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
}

.graph-node.high-risk {
  box-shadow: 0 0 5px var(--fraud-risk-color);
  animation: throb 2s infinite;
}

@keyframes throb {
  0% { transform: translate(-50%, -50%) scale(1); }
  50% { transform: translate(-50%, -50%) scale(1.3); }
  100% { transform: translate(-50%, -50%) scale(1); }
}

.graph-connection {
  position: absolute;
  opacity: 0.4;
  transform: rotate(45deg);
  transform-origin: top left;
  transition: all 0.5s ease;
  min-width: 1px;
  min-height: 1px;
}

.graph-connection.high-risk {
  opacity: 0.8;
}

.risk-cluster-highlight {
  position: absolute;
  top: 40%;
  left: 50%;
  width: 70px; /* Reduced from 80px */
  height: 70px; /* Reduced from 80px */
  border-radius: 50%;
  background: radial-gradient(circle, rgba(220, 53, 69, 0.15) 0%, rgba(220, 53, 69, 0) 70%);
  transform: translate(-50%, -50%);
  animation: highlight-pulse 2s infinite;
}

@keyframes highlight-pulse {
  0% { transform: translate(-50%, -50%) scale(1); opacity: 0.3; }
  50% { transform: translate(-50%, -50%) scale(1.2); opacity: 0.7; }
  100% { transform: translate(-50%, -50%) scale(1); opacity: 0.3; }
}

.graph-legend {
  display: flex;
  justify-content: center;
  padding: 8px 15px;
  border-top: 1px solid var(--border-color);
  background-color: rgba(0, 0, 0, 0.03);
  height: auto;
  min-height: 40px;
  flex-shrink: 0; /* Prevent legend from shrinking */
  margin-top: auto; /* Push to bottom of flex container */
  width: 100%; /* Ensure it spans full width */
  position: absolute; /* Position absolute to force to bottom */
  bottom: 0; /* Align to bottom */
  left: 0; /* Align to left */
}

