/* Fix alignment of metric cards in dark mode to match 2x2 grid in light mode */
.dark-theme .forecasting-dashboard .metrics-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  width: 100%;
}

.dark-theme .forecasting-dashboard .metrics-card {
  display: flex;
  align-items: center;
  margin: 0;
  width: 100%;
}

/* Fix horizontal alignment of forecast metrics in dark mode to match light mode */
.dark-theme .forecasting-dashboard .metrics-card {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding: 16px;
  min-height: 100px;
}

.dark-theme .forecasting-dashboard .metric-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
  width: 100%;
}

.dark-theme .forecasting-dashboard .metric-title {
  font-size: 0.75rem;
  font-weight: 500;
  line-height: 1.2;
}

.dark-theme .forecasting-dashboard .metric-value {
  font-size: 1.2rem;
  font-weight: 600;
