/* Global dark mode text adjustments for eye comfort */
.dark-theme {
  --text-color-bright: rgba(255, 255, 255, 0.87);
  --text-color-medium: rgba(255, 255, 255, 0.75);
  --text-color-soft: rgba(255, 255, 255, 0.65);
  --text-color-muted: rgba(255, 255, 255, 0.55);
}

.dark-theme body,
.dark-theme p,
.dark-theme span,
.dark-theme div {
  color: var(--text-color-medium);
}

.dark-theme h1,
.dark-theme h2,
.dark-theme h3,
.dark-theme h4,
.dark-theme h5,
.dark-theme h6 {
  color: var(--text-color-bright);
}

.dark-theme input,
.dark-theme textarea,
.dark-theme select {
  color: var(--text-color-bright);
}

.dark-theme a {
  color: var(--primary-light, #6366f1);
}

.dark-theme label,
.dark-theme .text-secondary {
  color: var(--text-color-soft);
}

.dark-theme .text-muted,
.dark-theme .small {
  color: var(--text-color-muted);
}

.dark-theme button:not(.primary-button):not([class*="action"]):not([class*="control"]) {
  color: var(--text-color-medium);
}

/* Dark mode styles for upload-success in Forecasting API */
.dark-theme .upload-success {
  background-color: rgba(16, 185, 129, 0.15);
  border-color: rgba(16, 185, 129, 0.3);
}

.dark-theme .success-icon {
