}

.dark-theme .forecasting-dashboard .action-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.15);
}

.dark-theme .forecasting-dashboard .action-button svg {
  color: rgba(255, 255, 255, 0.8);
}

.dark-theme .forecasting-dashboard .action-button span {
  color: rgba(255, 255, 255, 0.7);
}

/* Ensure form inputs in dark mode have proper colors */
.dark-theme .form-group input,
.dark-theme .form-group textarea {
  background-color: rgba(30, 30, 30, 0.8);
  color: var(--text-color-bright);
  border-color: rgba(255, 255, 255, 0.15);
